<template>
  <q-layout>
    <q-header>
      <q-toolbar class="app-header">
        <q-toolbar-title>Chat App</q-toolbar-title>
        <q-toolbar-title>Hello, {{username}}</q-toolbar-title>
      </q-toolbar>
    </q-header>

    <q-page-container>
      <q-page class="flex">
        <div class="q-pa-md row justify-center" style="width: 100%;">
          <div
            style="width: 100%;display: flex;flex-direction: column;justify-content: flex-end;"
          >
            <div class="message">
              <q-chat-message
                v-for="(message,index) in messageList"
                :key="index"
                :name="message.from"
                :text="[message.message]"
                :sent="message.sent"
              />
            </div>

            <div class="send-section">
              <q-input
                class="textInput"
                color="cadetblue"
                rounded
                outlined
                v-model="message"
                placeholder="Type your message"
                v-on:keyup.enter="submitMsg"
              ></q-input>
              <q-btn
                class="sendMessage"
                @click="submitMsg"
                round
                color="secondary"
                icon="navigation"
                style="background: #3A5894 !important;"
              />
            </div>
          </div>
        </div>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { QInput, QChatMessage } from "quasar";
import io from "socket.io-client";
const url="/"
const socket = io(url);
export default {
  name: "ChatWindow",
  components: {
    "q-input": QInput,
    "q-chat-message": QChatMessage
  },
  data() {
    return {
      message: "",
      from: "",
      receivedMessage: "",
      messageList: []
    };
  },
  props:{
    username:String
  },
  created(){
    this.from=this.username;
  },
  mounted() {
    this.getMessage();
  },
  methods: {
    getMessage: function() {
      socket.on("getMessage", data => {
        data.sent = true;
        this.messageList.push(data);
      });
    },
    submitMsg: function() {
      let messageParam = {
        from: this.from,
        message: this.message
      };
      this.messageList.push(messageParam);
      this.message = "";
      socket.emit("submitMessage", messageParam);
    }
  }
};
</script>
<style scoped>
.sendMessage {
  background: rgb(58, 88, 148) !important;
  transform: rotate(90deg);
  height: 3.4em !important;
  width: 4em !important;
  margin-left: 10px;
}
.send-section {
  display: flex;
}
.textInput {
  align-self: flex-end;
  width: 100%;
}
.app-header {
  background-color: #3a5894;
  font-family: "Acme", sans-serif;
}
</style>